* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; 
  }
  
  body {
    animation: Gradient 15s ease infinite;
    background: linear-gradient(117deg, rgb(0 24 34) 0%, rgb(32 0 0) 50%, rgb(9 32 0) 100%);
    background-size: 400% 400%;
    overflow: hidden;
    padding: 0 0px;
    width: 100%;
    overflow: hidden;
    font-family: 'Spartan', sans-serif !important;
    color: white;
  }

  @keyframes Gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }

  tr{
    display: table;
  }

  .overlay{
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 20%, rgba(0,212,255,0) 100%);
    position: fixed;
    height: 100vh;
    width: 100vw;
    z-index: -1;
  }
  
  header {
    margin: auto;
    padding: 16px 32px;
  }
  
  header h1 {
    color: rgb(0, 0, 0);
    font-family: "McLaren", cursive;
    font-weight: 200;
  }

  footer {
    position: fixed;
    text-align: center;
    bottom: 0px;
    width: 100%;
    height: 2.5rem;
    background-color: black;
    box-shadow: 0 -10px 5px 0 rgba(0, 0, 0, 0.1);
  }
  
  footer p {
    color: rgb(255, 255, 255);
    align-items: center;
    margin: .75em;
  }

  .title, a{
    display: inline-block;
    text-decoration: none;
    color: inherit;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  a:hover, .title:hover{
    transform: scale(1.1)!important;
  }

  .backgroundImage
  {
    background-size: cover;
    min-width: 100vw !important;
    height: 100vh;
    color: red;
    position: fixed;
    top: 0;
    z-index: -1;
  }

  .pageContainer
  {
    margin: 100px;
  }

  .PageContent
  {
    font-size: 2em;
    line-height: 2em;
    color: white;
    width: 700px;
    transition: 0.5s ease-in;
    text-shadow: 0.5px 0.5px black;
    max-height: 50vh;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    overflow-y: scroll; /* Add the ability to scroll */
  }

  .PageContent::-webkit-scrollbar {
    display: none;
  }

  .gameTitle{
    font-weight: 800;
    font-size: 1.1em;
  }

  .releaseYear{
    font-weight: 100;
    font-size: .75em;
  }

  .positionDescription
  {
    font-style: italic;
    font-weight: 100;
    font-size: 0.75em;
  }

  .PageContent:hover
  {
    opacity: 1;
  }

  .platformLogo
  {
    padding: 10px 20px 5px 0px;
    display: flex;
  }

  .Container
  {
    overflow: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    display: inline-block;
    max-height: 50vh;
  }

  .Container::-webkit-scrollbar {
    display: none;
}
  .dictionary {
    display: flex;
    opacity: 10%;
    transition: 0.5s;
    transition: 0.5s ease-in;
  }
  
  .dictionary:hover{
    opacity: 100%;
  }
  
  .dictionary > .term {
    margin-bottom: 60px;
    margin-right: 25px;
    border-radius: 3px;
    background-color: #fff;
    transition: all 100ms ease-in-out;
    display: block;
    line-height: 0;
  }

  @media screen and (max-width: 825px){
    .dictionary > .term {
        margin-bottom: 15%;
    }
  }

  .contentTitle
  {
    color: white;
    margin: 20px 0px 5px;
    text-shadow: 0.5px 0.5px black;
  }

  .textDescription
  {
    font-family: 'Assistant', sans-serif;
  }

  .imageThumbnail
  {
    width: 30em;
    z-index: -1;
  }
  
  .imageThumbnail:hover{
    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;    z-index: 1;
  }

  .work {
    display: block;
    font-size: 200%;
    text-align: center;
  }

  .slick-list
  {
    width: 90vw;
    padding: 15px !important;
  }

  .slick-track
  {
    width: 5000px !important;
  }

  .slick-slide
  {
    width: 500px !important;
  }

  .slick-prev
  {
    top: -25px !important;
    left: inherit !important;
    right: 50px !important;
    z-index: 1 !important;
  }

  .slick-prev::before
  {
    font-size: 30px !important;
  }

  .slick-next
  {
    z-index: 1 !important;
    right: 10px !important;
    top: -25px !important;
  }

  .slick-next::before
  {
    font-size: 30px !important;
  }
  
  /* Contact Page */

  .feedback-input {
    color:white;
    font-family: Helvetica, Arial, sans-serif;
    font-weight:500;
    font-size: 18px;
    border-radius: 5px;
    line-height: 22px;
    background-color: transparent;
    border:2px solid #ffffff;
    transition: all 0.3s;
    padding: 13px;
    margin-bottom: 15px;
    width:100%;
    box-sizing: border-box;
    outline:0;
  }

  .inputTitle
  {
    color: white;
    font-size: 1.2em;
    padding: 10px 0 5px 0px;
    display: block;
  }
  
  .feedback-input:focus { border:2px solid #9b7909; }
  
  textarea {
    height: 150px;
    line-height: 150%;
    resize:vertical;
  }

  .submitButton
  {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    width: 100%;
    border-radius:5px;
    border:0;
    color: white;
    font-size: 24px;
    padding-top:10px;
    padding-bottom:10px;
    transition: all 0.3s;
    margin-top:-4px;
    font-weight:700;
    pointer-events: none;
    animation: Gradient 15s ease infinite !important;
    background: linear-gradient(117deg, rgb(0 116 165) 0%, 50%, rgb(38 131 1) 100%);
    background-size: 400% 400% !important;
  }

  @keyframes Gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }

  button:disabled
  {
    opacity: 20% !important;
    background:#b32121 !important;
    cursor:pointer!important;
    pointer-events: none !important;
  }

  button:enabled
  {
    opacity: 100% !important;
    cursor:pointer !important;
    pointer-events:inherit !important;
  }

  button:enabled:hover
  { 
    transform: scale(1.05);
  }

.ContactPage
{
  width: 90%;
  margin: auto;
  padding: 2.5%;
}

.informationTitle
{
  font-size: x-large;
  padding: 20px 0 10px;
}

.informationText
{
  font-family: 'Assistant';
  font-size: 1.2em;
  letter-spacing: .1em;
}

.titleSection
{
  margin: auto;
}

.ContactTitle
{
  color: white;
    display: block;
    text-align: center;
    padding: 0 0 50px
}

.tableColumn
{
  vertical-align: top;
  padding: 20px 30px 20px 0;
}

.ContactContainer
{
  padding: 2.5% 0;
  background: #000000a8;
  border-radius: 2.5%;
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-content: space-around;
  flex-direction: row;
}


.ContactForm
  {
    width: fit-content;
  }

  /* Navbar Test */
  .navbar {
	height: 80px;
	width: 100%;
  background: linear-gradient(180deg, rgba(0,0,0,.85) 0%, rgba(0,0,0,.9) 80%, rgba(0,212,255,0) 100%);
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
  margin: auto;
  padding: 16px 48px;
}

.nav-logo {
  margin-left: 55px;
	text-decoration: none;
	color: white;
  font-size: 2em;
}

.nav-links {
	display: grid;
	grid-template-columns: repeat(4, auto);
	grid-gap: 30px;
	list-style: none;
}

.nav-link {
	text-decoration: none;
	color: white;
	transition: 0.3s all;
  font-family: "McLaren", cursive;
}

.nav-link:hover {
	color: red;
}

.nav-icon {
	display: none;
	font-size: 2rem;
	cursor: pointer;
}

.navbarLogo
{
  width: 40px;
}

@media only screen and (max-width: 900px) {
	.navbar {
		position: relative;
	}

	.nav-links {
		display: flex;
		flex-direction: column;
		position: absolute;
		text-align: center;
		width: 100%;
		top: 80px;
		left: -100%;
		transition: 0.5s all;
	}

	.nav-links.active {
		background: rgb(0, 0, 0);
		left: 0;
	}

	.nav-item {
		padding: 10px 0;
    border-style: solid;
    border-color: #fff;
    border-width: 1px 0px 1px 0px;
	}

	.nav-icon {
		display: flex;
	}
}